nav {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  align-content: center;
  justify-content: center;
}
.back {
  background-image: url("../../img/pop_culter_dog_rank_alyceatinoyan_feature.jpg");
  background-size: 100% 52em;
  margin-top: 100px;
  background-repeat: no-repeat;
}
.backCreate{
  background-image: url("../../img/pop_culter_dog_rank_alyceatinoyan_feature.jpg");
  background-size: 100% 52em;
  margin-top: 100px;
  background-repeat: no-repeat;
}
.wrapper {
  width: 100%;
  position: fixed;
  margin: auto;
  z-index: 5;
  top: 0;
  background-color: #14213d;
  box-shadow: 9px 8px 3px 3px rgb(0 0 1);
}

nav a {
  display: flex;
  color: #cfc9c8;
  text-decoration: none;
  padding: 15px 30px;
  font-size: 20px;
  font-weight: bold;
  -webkit-transition: all 100ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
}

nav a:hover {
  background-color: #f56f3a;
  border-radius: 50px;
}
.select {
  position: relative;
  border: 5px solid #ccc;
  width: 120px;
  overflow: hidden;
  background-color: #fff;
  margin-left: 30px;
}

.select:before {
  content: "";
  position: absolute;
  right: 5px;
  top: 7px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 5px 0 5px;
  border-color: #14213d transparent transparent transparent;
  z-index: 5;
  pointer-events: none;
}

.select select {
  padding: 15px 8px;
  width: 130%;
  border: none;
  box-shadow: none;
  background-color: transparent;
  background-image: none;
  appearance: none;
}
